* {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}

/*스크롤 고정 및 html, body 전체영역*/
html, body {
    height: 100%;
    width:100%;
    overflow: hidden;
}
#root{
    overflow: auto;
    height: 100%;
    text-align: center;
}

#root > h2{
    text-align: center;
    margin-top: 35px;
    font-size: 36pt;
    font-weight: 700;
}
#root > .center_box{
    width: 550px;
    height: 250px;
    box-shadow: 0 0 15px 35px;
    display: inline-block;
    position: relative;
}
#root > form > label{
    font-size: 22pt;
    font-weight: 700;
}

.footer{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100px;
    line-height: 28px;
    font-size: 14pt;
    padding: 10px;
    text-align: center;
    background: #282c34;
    color: #eee;
}
.footer > font{
    font-weight: 700;
    display: block;
}

input[type=text]{
    border: 0;
    outline: none;
    padding-left: 5px;
    border-bottom: 2px solid #333;
    width: calc(100% - 120px);
}

button{
    border: none;
    padding: 10px 20px;
    font-weight: 700;
    transition: 0.5s;
    color: #333;
    background: #61dafb;
}
button:hover{
    transform: scale(1.5);
    cursor: pointer;
}

.btn_div{
    text-align: right;
    margin-top: 20px;
}