body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: "MapleStory";
    font-style: normal;
    font-weight: 700;
    src: url(/static/media/MaplestoryBold.b0a6a269.ttf);
}

@font-face {
    font-family: "MapleStory";
    font-style: normal;
    font-weight: normal;
    src: url(/static/media/MaplestoryLight.fb932d04.ttf);
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
input, select,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    font-family: 'MapleStory', sans-serif, Consolas, Serif;
    font-style: normal;
    font-weight: normal;
}
@CHARSET "UTF-8";

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
* {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}

/*스크롤 고정 및 html, body 전체영역*/
html, body {
    height: 100%;
    width:100%;
    overflow: hidden;
}
#root{
    overflow: auto;
    height: 100%;
    text-align: center;
}

#root > h2{
    text-align: center;
    margin-top: 35px;
    font-size: 36pt;
    font-weight: 700;
}
#root > .center_box{
    width: 550px;
    height: 250px;
    box-shadow: 0 0 15px 35px;
    display: inline-block;
    position: relative;
}
#root > form > label{
    font-size: 22pt;
    font-weight: 700;
}

.footer{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100px;
    line-height: 28px;
    font-size: 14pt;
    padding: 10px;
    text-align: center;
    background: #282c34;
    color: #eee;
}
.footer > font{
    font-weight: 700;
    display: block;
}

input[type=text]{
    border: 0;
    outline: none;
    padding-left: 5px;
    border-bottom: 2px solid #333;
    width: calc(100% - 120px);
}

button{
    border: none;
    padding: 10px 20px;
    font-weight: 700;
    transition: 0.5s;
    color: #333;
    background: #61dafb;
}
button:hover{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    cursor: pointer;
}

.btn_div{
    text-align: right;
    margin-top: 20px;
}
